.container {
    border-radius: 10px;
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.container>* {
    font-size: 16px;
    color: blue;
}

.desc {
    font-size: 16px;
    grid-column: span 2;
    color: black;
}