.container {
    position: absolute;
    background: white;
    border-radius: 10px;
    padding: 10px;
    top: 50%;
    left: 50%;
    width: 600px;
    height: 400px;
    margin-top: -200px;
    margin-left: -300px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    display: grid;
    place-items: center;
}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 20px;
    place-items: center;
    
}


.input {
    height: 20px;    
    width: 100%;
}

.descLabel {
    align-self: start;
}

.titleBox {
    display: flex;
}